
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after{content:'';content:none
}
q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
button:focus{outline:0
}
@keyframes fadeIn{
0%{opacity:0
}
100%{visibility:visible;opacity:0.7
}
}
.memo{display:-ms-flexbox;display:flex
}
.memo-cards-container{margin-top:20px;background:#000;width:75%;max-width:1000px;margin-left:5%;padding:0;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:distribute;justify-content:space-around;perspective:1000px;border-radius:10px 0 0 10px
}
.memo-card{width:calc(16.6666% - 40px);height:160px;margin:10px;transition:0.6s;transform-style:preserve-3d;position:relative
}
@media only screen and (max-width: 1152px){
.memo-card{width:calc(25% - 40px);height:140px
}
}
@media only screen and (max-width: 680px){
.memo-card{width:calc(33.3333% - 40px);height:100px
}
}
@media only screen and (max-width: 480px){
.memo-card{width:calc(33.3333% - 40px);height:100px
}
}
@media only screen and (max-width: 380px){
.memo-card{width:calc(50% - 40px);height:100px
}
}
.memo-card__front,.memo-card__back{border-radius:12px;-webkit-backface-visibility:hidden;backface-visibility:hidden;background-size:100% 100%;background-repeat:no-repeat;position:absolute;top:0;left:0;height:100%;width:100%
}
.memo-card__front{background-image:url("./assets/card.svg");z-index:2
}
.memo-card__front--transform{transform:rotateY(180deg)
}
.memo-card__front:hover{cursor:pointer
}
.memo-aside{background:#19292A;margin-top:20px;padding:10px;color:white;text-align:center;margin-rigth:5%;width:15%;border-radius:0 10px 10px 0
}
.memo-aside__score,.memo-aside__move{font-size:22px;font-size:1.375rem;background:black;margin-bottom:2px
}
@media only screen and (max-width: 680px){
.memo-aside__score,.memo-aside__move{font-size:14px;font-size:.875rem
}
}
.memo-aside__button{font-size:22px;font-size:1.375rem;background:#69a2a6;width:100%;height:40px;border:1px solid black;margin-bottom:2px;border-radius:5px;font-weight:500
}
@media only screen and (max-width: 680px){
.memo-aside__button{font-size:14px;font-size:.875rem
}
}
.memo-aside__button:hover{cursor:pointer;background:#c9dedf
}
.memo-aside__button--level{display:block
}
.memo-score{position:absolute;background:black;opacity:0.8;width:100%;height:100%;border-radius:5px;border:1px solid black;visibility:hidden
}
.memo-score--active{visibility:visible;animation:2s fadeIn;animation-fill-mode:forwards
}
.memo-score__win{background:white;margin-top:15%;text-align:center;height:80px;line-height:80px;font-size:2em;font-weight:bold
}
.memo-score__again{margin-top:30px;text-align:center;height:80px;line-height:80px;font-size:2em;font-weight:bold;width:100%
}
.memo-score__again:hover{cursor:pointer
}
.memo-aside-lastScores{color:white;font-size:18px;border:1px solid white;margin:5px;padding:2px
}



